/* ==========================================================================
   WooCommerce Message styles - begin
   ========================================================================== */
.qodef-woocommerce-page {

    .woocommerce-message,
    .woocommerce-info,
    .woocommerce-error {
        position: relative;
        display: block;
        padding: 20px 30px;
        margin: 0 0 40px;
        line-height: 49px;
        list-style: none;
        background-color: transparent;
        border: 1px solid $default-border-color;
        
        @include ipad-landscape {
            line-height: 39px;
        }

        > a {
            color: $default-text-color;

            &:hover {
                color: $default-heading-color;
            }
        }

        a {
            float: right;
        }
    }

    .woocommerce-info {

        .showcoupon {
            color: $default-heading-color;
            position: relative;
            font-size: 20px;
            
            &:after {
                content: '';
                position: absolute;
                left: 0;
                bottom: 13px;
                width: 100%;
                height: 1px;
                background-color: currentColor;
                
                @include ipad-landscape {
                    bottom: 8px;
                }
                
                @include phone-landscape {
                    bottom: 0;
                }
            }

            &:hover {
                color: currentColor;
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Message styles - end
   ========================================================================== */