/* ==========================================================================
   WooCommerce Single Product Page styles - begin
   ========================================================================== */

.qodef-woo-single-page {
	
	&.qodef-woo-single-thumb-on-left-side {
		
		.qodef-single-product-content {
			
			.images {
				
				@media only screen and (min-width: $ipad-portrait-plus-pixel) {
					
					figure {
						width: 100%;
						margin: 0;
						overflow: hidden;
					}
					
					$thumb_size: 14.6;
					$space_between_items: 20;
					
					.woocommerce-product-gallery__image {
						
						&:first-child {
							$first_thumb_offset: $thumb_size + $space_between_items;
							
							left: calc(14.6% + 20px);
							width: calc(85.4% - 20px);
						}
					}
					
					&.woocommerce-product-gallery--with-images {
						
						.woocommerce-product-gallery__image {
							
							&:not(:first-child) {
								position: absolute;
								top: 0;
								left: 0;
								width: 14.6%;
								padding: 0 0 #{$space_between_items}px;
								box-sizing: border-box;
								
								a {
									height: 100%;
									
									img {
										height: 100%;
									}
								}
							}
						}
					}
					
					$thumb_3_columns_label: ('columns-2', 'columns-3', 'columns-4');
					@for $i from 0 to length($thumb_3_columns_label) {
						&.woocommerce-product-gallery--#{nth($thumb_3_columns_label, $i+1)} {
							$column_value: $i+2;
							$column_size: 100 / $column_value;
							$column_width: calc(#{100% / $column_value} + #{$space_between_items / $column_value}px);
							
							.woocommerce-product-gallery__image {
								
								&:not(:first-child) {
									height: $column_width;
								}
								
								$thumbnail_numbers: (2, 3, 4, 5, 6, 7, 8, 9);
								@for $i from 0 to length($thumbnail_numbers) {
									$thumbnail_number_value: nth($thumbnail_numbers, $i+1);
									
									&:nth-child(#{$thumbnail_number_value}#{n}) {
										$first_param: ($column_size * $i)#{'%'};
										$second_param: ($space_between_items / $column_value * $i)#{px};
										$topPos: calc(#{$first_param} + #{$second_param});
										
										top: $topPos;
									}
								}
							}
						}
					}
				}
			}
		}
	}
    
    .product {
	    @include qodefRelativeHolderLayout();
	    float: none;
	    margin: 0 0 50px;
    }
        
    .qodef-single-product-content {
        @include qodefRelativeHolderLayout();
        clear: both;
        margin: 0 0 20px;
	
	    .images {
		    position: relative;
		    display: inline-block;
		    vertical-align: middle;
		    width: 55%;
		    margin: 0;
		    padding: 0 29px 0 0;
		    float: left;
		    box-sizing: border-box;
		    
		    figure {
			    position: relative;
			    display: inline-block;
			    vertical-align: top;
			    margin: 0 -10px;
			
			    a {
				    @include qodefRelativeHolderLayout();
				
				    img {
					    position: relative;
					    width: 100%;
					    display: block;
				    }
			    }
		    }
		
		    .woocommerce-product-gallery__trigger {
				position: absolute;
			    top: 10px;
			    right: 39px;
			    width: 30px;
			    height: 30px;
			    line-height: 30px;
			    text-align: center;
			    z-index: 10;
		    }
		    
		    .woocommerce-product-gallery__image {
			    position: relative;
			    display: inline-block;
			    vertical-align: top;
			    margin: 0;
			    padding: 0 10px;
			    box-sizing: border-box;
			    
			    &:first-child {
				    left: 10px;
				    width: calc(100% - 20px);
				    padding: 0;
			    }
			
			    .zoomImg {
				    cursor: crosshair;
			    }
		    }
		    
		    &.woocommerce-product-gallery--with-images {
			
			    .woocommerce-product-gallery__image {
				
				    &:not(:first-child) {
					    float: left;
					    padding-top: 20px;
				    }
			    }
		    }
		
		    $thumb_3_columns_label: ('columns-2', 'columns-3', 'columns-4');
		    @for $i from 0 to length($thumb_3_columns_label) {
			    &.woocommerce-product-gallery--#{nth($thumb_3_columns_label, $i+1)} {
				    $column_width: 100%/($i+2);
				
				    .woocommerce-product-gallery__image {
					
					    &:not(:first-child) {
						    width: $column_width;
					    }
				    }
			    }
		    }
	    }
    }
	
    .qodef-single-product-summary {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        width: 45%;
        padding: 0 0 0 29px;
        float: left;
        box-sizing: border-box;
	
	    .qodef-single-product-title {
		    display: block;
		    margin: 0 0 13px;
	    }
	
	    .price {
		    @include qodefWooCommercePriceStyle();
	    }
	
	    .woocommerce-product-rating {
		    @include qodefRelativeHolderLayout();
		    line-height: 1;
		    margin: 5px 0 15px;
		    
		    .woocommerce-review-link {
			    margin-left: 15px;
			    font-size: 14px;
				color: $default-text-color;
		    }
		
		    .star-rating {
			    width: 98px;
			    line-height: 1.25em;
			    
			    &:before {
				    letter-spacing: 7px;
			    }

			    span {
				    &:before {
					    letter-spacing: 7px;
				    }
			    }
		    }
	    }
	
	    button[type=submit] {
		    &.single_add_to_cart_button {
			    @include qodefButtonOutlineColor();
			    font-size: 20px;
		    }
	    }
	    
	    .woocommerce-product-details__short-description {
		    @include qodefRelativeHolderLayout();
		    margin: 25px 0 0;
		
		    p {
			    margin: 0;
		    }
	    }
	
	    .product_meta {
		    margin: 51px 0 0;
		    font-size: 17px;
		    color: $default-heading-color;
		
		    > span {
			    display: block;
			    margin: 3px 0 7px;
			
			    a {
				    color: $default-heading-color;
				
				    &:hover {
					    color: $first-main-color;
				    }
			    }
		    }
	    }
	
	    .qodef-woo-social-share-holder {
		    @include qodefRelativeHolderLayout();
		    margin: 0;
		
		    > * {
			    display: table-cell;
			    vertical-align: top;
		    }
		
		    > span {
			    padding: 0 9px 0 0;
		    }
	    }
	
	    p.stock.out-of-stock,
	    p.stock.in-stock {
		    color: $default-heading-color;
		    font-weight: bold;
		    margin: 18px 0 0;
	    }
	
	    form.cart {
		    @include qodefRelativeHolderLayout();
		    margin: 43px 0 0;
	    }
	
	    .qodef-quantity-buttons {
		    margin: 0 16px 0 0;
	    }
    }
	
	.woocommerce-tabs {
		@include qodefRelativeHolderLayout();
		margin: 70px 0 0;
		
		.qodef-product-tabs-line {
			position: absolute !important;
			left: 0;
			bottom: 6px;
			height: 0;
			border-top: 2px solid #333333;
			transition: width .4s, left .6s cubic-bezier(0.25, 0.1, 0.11, .99);
			
			@include phone-landscape {
				display: none !important;
			}
		}
		
		ul.tabs {
			@include qodefRelativeHolderLayout();
			list-style: none;
			margin: 0;
			padding: 0;
			border-bottom: 1px solid $default-border-color;
			text-align: left;
			clear: both;
			
			> li {
				position: relative;
				display: inline-block;
				vertical-align: middle;
				
				@include phone-landscape {
					&.active {
						a {
							&:after {
								opacity: 1;
							}
						}
					}
				}
				
				a {
					position: relative;
					display: inline-block;
					vertical-align: top;
					padding: 13px 15px 21px;
					font-size: 18px;
					font-weight: bold;
					
					&:hover {
						color: $default-heading-color;
					}
					
					@include phone-landscape {
						&:after {
							content: '';
							position: absolute;
							width: 100%;
							height: 2px;
							background-color: $default-heading-color;
							left: 0;
							bottom: 6px;
							opacity: 0;
							transition: 0.3s opacity ease;
						}
					}
				}
			}
		}
		
		.entry-content {
			@include qodefRelativeHolderLayout();
			
			> h2 {
				display: none;
			}
			
			> p {
				margin: 30px 0 20px;
			}
		}
		
		table {
			margin: 18px 0 20px;
			
			tr, th {
				border: 0;
			}
			
			td, th {
				padding: 3px 0;
				vertical-align: middle;
				text-align: left;
				
				p {
					margin: 0;
				}
			}
			
			th {
				color: $default-heading-color;
				width: 1%;
				padding: 0 10px 0 0;
			}
			
			td {
				width: 99%;
			}
		}
		
		#reviews {
			@include qodefRelativeHolderLayout();
			margin: 18px 0 20px;
			
			h2 {
				font-size: 18px;
				line-height: 24px;
				margin: 8px 0 24px;
			}
			
			ol.commentlist {
				list-style: none;
				
				li {
					margin: 0 0 15px;
					
					&:last-child {
						margin: 0;
					}
				}
				
				.comment_container {
					@include qodefRelativeHolderLayout();
					
					img {
						float: left;
						width: 70px;
					}
				}
				
				.comment-text {
					padding: 0 0 0 90px;
					
					p {
						margin: 0;
					}
				}
			}
			
			.star-rating {
				span {
					font-size: 0;
					
					&:before {
						font-size: 13.5px;
						letter-spacing: 7px;
					}
				}
				
				&:before {
					letter-spacing: 7px;
				}
			}
			
			.comment-respond {
				margin-top: 15px;
				
				.comment-reply-title {
					margin: 0 0 10px;
				}
				
				p {
					margin: 0;
				}
				
				.stars {
					
					> span {
						position: relative;
						display: flex;
						margin: 10px 0;
					}
					
					&.selected {
					
						a {
							@include qodefWooCommerceSingleProductRatingsStyle(true);
						}
					}
					
					a {
						position: relative;
						display: block;
						color: transparent;
						margin: 0;
						padding-right: 7px;
						font-size: 0;
						line-height: 1;
						@include qodefWooCommerceSingleProductRatingsStyle();
					}
				}
			}
			
			.comment-form-comment {
				textarea {
					height: 38px;
				}
			}
			
			%line {
				content: '';
				position: absolute;
				left: 32px;
				right: 32px;
				bottom: 13px;
				height: 1px;
				z-index: 10;
				background-color: #fff;
				
				@include ipad-landscape {
					left: 20px;
					right: 20px;
					bottom: 8px;
				}
			}
			
			.form-submit {
				position: relative;
				display: inline-block;
				
				&:before {
					@extend %line;
					@include qodefTransform(scaleX(0));
					transform-origin: 0 0;
				}
				
				&:after {
					@extend %line;
					transform-origin: 100% 0;
				}
				
				&:hover {
					&:before {
						@include qodefTransform(scaleX(1));
						transition: transform .9s cubic-bezier(0.19, 1, 0.22, 1) .25s;
					}
					
					&:after {
						@include qodefTransform(scaleX(0));
						transition: transform .9s cubic-bezier(0.19, 1, 0.22, 1);
					}
				}
				
				input[type=submit] {
					margin-top: 15px;
					-webkit-border-radius:0;
					border-radius:0;
					-webkit-appearance: none;
				}
			}
		}
		
		.woocommerce-product-attributes {
			th, td {
				font-size: 17px;
				font-weight: 400;
			}
		}
	}
	
	.related.products,
	.upsells.products {
		margin: 28px 0 0;
		
		> h2 {
			display: block;
			margin: 41px 0 30px;
			font-size: 35px;
			text-align: left;
		}
		
		.product {
			
			.qodef-pl-text-wrapper {
				margin-bottom: 0;
			}
			
			.qodef-pl-category {
				line-height: 1.5em;
			}
		}
	}
}

.qodef-ms-explorer {
	&.qodef-woo-single-page {
		.qodef-single-product-summary {
			.woocommerce-product-rating {
				.star-rating {
					width: 105px;
				}
			}
		}
	}
}
/* ==========================================================================
   WooCommerce Single Product Page styles - end
   ========================================================================== */