/* ==========================================================================
   Marks (Sale, Out of Stock) mixin styles - begin
   ========================================================================== */

@mixin qodefWooCommerceMarksGeneralStyle() {
    position: absolute;
    display: block;
    top: 0;
    width: auto;
    height: 33px;
    margin: 0;
    padding: 4px 15px;
    color: #fff;
    font-size: 17px;
    line-height: 24px;
    text-align: center;
    z-index: 100;
    box-sizing: border-box;
    transform: translateZ(0);
    
    &:after {
        content: '';
        display: block;
        position: absolute;
        left: 15px;
        bottom: 7px;
        width: calc(100% - 30px);
        height: 1px;
        background-color: currentColor;
    }
}

@mixin qodefWooCommerceSaleStyle() {
    left: 0;
    background-color: $default-heading-color;
}

@mixin qodefWooCommerceOutOfStockStyle() {
    right: 0;
    background-color: $default-heading-color;
}

@mixin qodefWooCommerceNewProductStyle() {
    right: 0;
    background-color: $default-heading-color;
}

/* ==========================================================================
   Marks (Sale, Out of Stock) mixin styles - end
   ========================================================================== */

/* ==========================================================================
   Price mixin styles - begin
   ========================================================================== */

@mixin qodefWooCommercePriceStyle() {
    position: relative;
    display: block;
    line-height: 1;
    color: $default-heading-color;
    
    * {
        font-size: 20px;
        line-height: inherit;
        font-weight: bold;
    }
    
    ins {
        margin: 0 0 0 8px;
        text-decoration: none;
    }
}
/* ==========================================================================
   Price mixin styles - end
   ========================================================================== */

/* ==========================================================================
   Star Ratings mixin styles - begin
   ========================================================================== */

%star-ratings {
    font-family: "ElegantIcons";
    font-size: 13.5px;
    line-height: 1;
    letter-spacing: 2px;
    color: $first-main-color;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

@mixin qodefWooCommerceRatingsStyle() {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    @extend %star-ratings;
    overflow: hidden;
    
    &:before {
        content: "\e031\e031\e031\e031\e031";
    }
    
    span {
        position: absolute;
        top: 0;
        left: 0;
        overflow: hidden;
        
        &:before {
            content: "\e033\e033\e033\e033\e033";
        }
    }
}

@mixin qodefWooCommerceSingleProductRatingsStyle($active-style: false) {
    $single-rating-default-icon: "\e031";
    $single-rating-active-icon: "\e033";
    
    @if ($active-style) {
    
        &.active {
    
            &:before {
                content: $single-rating-active-icon;
            }
            
            ~ a {
    
                &:before {
                    content: $single-rating-default-icon;
                }
            }
        }
        
        &:not(.active) {
            
            &:before {
                content: $single-rating-active-icon;
            }
        }
    } @else {
        
        &:before {
            content: $single-rating-default-icon;
            @extend %star-ratings;
        }
    }
}
/* ==========================================================================
   Star Ratings mixin styles - end
   ========================================================================== */