/* ==========================================================================
   WooCommerce Dropdown Cart styles - begin
   ========================================================================== */

.qodef-shopping-cart-holder {
    position: relative;
    display: inline-block;
    vertical-align: top;
    height: 100%;
    margin: 0 10px;

    .touch & {
        cursor: pointer;
    }
    
    &:hover {
        
        .qodef-sc-dropdown {
            max-height: 100vh;
            pointer-events: auto;
            transition: .5s cubic-bezier(0.66, 0.14, 0.58, 1) .1s;
        }
    }

    .qodef-shopping-cart-inner {
        @include qodefRelativeHolderLayout();
        height: 100%;
    }

    .qodef-header-cart {
        position: relative;
        display: flex;
        align-items: center;
        height: 100%;
        transition: opacity 0.3s ease-in-out;
    
        &:hover {
            color: initial;
            opacity: 0.85;
        }

        .qodef-sc-opener-icon {
            position: relative;
            display: inline-block;
            vertical-align: top;
            line-height: 1;
        }
    
        .qodef-shopping-cart-label {
            padding: 0 5px;
        }

        .qodef-shopping-cart-label,
        .qodef-sc-opener-count {
            font-size: 16px;
            font-weight: 600;
        }

        &.qodef-header-cart-icon-pack {
            
            .qodef-sc-opener-icon {
                font-size: 18px;
    
                * {
                    display: block;
                    line-height: inherit;
        
                    &:before {
                        display: block;
                        line-height: inherit;
                    }
                }
            }
        }

        &.qodef-header-cart-svg-path {
            
            .qodef-sc-opener-icon {

                > * {
                    vertical-align: middle;
                }
                
                > *, 
                path {
                    fill: currentColor;
                }
            }
        }
    }
}

.qodef-dark-header .qodef-page-header > div:not(.qodef-sticky-header):not(.fixed) .qodef-shopping-cart-holder .qodef-header-cart {
    color: $header-dark-color;
    transition: opacity 0.3s ease-in-out;

    &:hover {
        opacity: 0.85;
    }
}

.qodef-light-header .qodef-page-header > div:not(.qodef-sticky-header):not(.fixed) .qodef-shopping-cart-holder .qodef-header-cart {
    color: $header-light-color;
    transition: opacity 0.3s ease-in-out;

    &:hover {
        opacity: 0.85;
    }
}

.qodef-sc-dropdown {
    position: absolute;
    top: 100%;
    left: auto;
    right: 0;
    text-align: left;
    box-sizing: border-box;
    max-height: 0;
    pointer-events: none;
    overflow: hidden;
    will-change: transform;
    transition: .2s;
    
    .qodef-sc-dropdown-inner {
        @include qodefRelativeHolderLayout();
        width: 308px;
        padding: 34px 30px 34px 34px;
        background-color: #fff;
        box-sizing: border-box;
        border: 1px solid #ebebeb;
    }
    
    .qodef-empty-cart {
        color: $default-text-color;
    }
    
    .qodef-sc-dropdown-items {
        @include qodefRelativeHolderLayout();
    }
    
    .qodef-sc-dropdown-item {
        position: relative;
        display: flex;
        align-items: flex-start;
        margin-top: 20px;
        
        &:first-child {
            margin-top: 0;
        }
    }
    
    .qodef-sc-dropdown-item-image {
        flex-shrink: 0;
        width: 44px;
        margin-right: 10px;
        
        a, img {
            display: block;
        }
    }
    
    .qodef-sc-dropdown-item-content {
        @include qodefRelativeHolderLayout();
        padding-right: 20px;
    }
    
    .qodef-sc-dropdown-item-title {
        margin: 0;
        font-size: 18px;
        
        a {
            &:hover {
                color: currentColor;
            }
        }
    }
    
    .qodef-sc-dropdown-item-price {
        margin: 3px 0 0;
        font-size: 14px;
        line-height: 1em;
        color: $default-text-color;
    }
    
    .qodef-sc-dropdown-item-quantity {
        margin: 3px 0 0;
    }
    
    .qodef-sc-dropdown-item-remove {
        position: absolute;
        top: -5px;
        right: 0;
        font-size: 16px;
        line-height: 1;
    }
    
    .qodef-sc-dropdown-subtotal {
        position: relative;
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-top: 20px;
        
        * {
            font-size: 20px;
            font-weight: bold;
            color: $default-heading-color;
        }
        
        > * {
            margin: 0;
        }
    }
    
    .qodef-sc-dropdown-button-holder {
        @include qodefRelativeHolderLayout();
        display: flex;
        margin-top: 20px;
    }
    
    .qodef-sc-dropdown-button {
        @include qodefButtonDefaultStyle();
        display: block;
        width: 100%;
        text-align: center;
        padding: 5px 0;
        font-size: 15.69px;
    
        %line {
            content: '';
            display: block;
            position: absolute;
            left: -1px;
            bottom: 0;
            width: calc(100% + 2px);
            height: 1px;
            background-color: currentColor;
        }
    
        span {
            position: relative;
    
            &:before {
                @extend %line;
                @include qodefTransform(scaleX(0));
                transform-origin: 0 0;
            }
    
            &:after {
                @extend %line;
                transform-origin: 100% 0;
            }
        }
        
        &:hover {
            @include qodefButtonSolidHoverColor();
            
            span {
                &:before {
                    @include qodefTransform(scaleX(1));
                    transition: transform .9s cubic-bezier(0.19, 1, 0.22, 1) .25s;
                }
    
                &:after {
                    @include qodefTransform(scaleX(0));
                    transition: transform .9s cubic-bezier(0.19, 1, 0.22, 1);
                }
            }
            
        }
        
        &:first-child {
            @include qodefButtonSolidColor();
        }
        
        &.qodef-view-cart-button {
            @include qodefButtonOutlineColor();
            margin-left: 7px;
        }
    }
}

.qodef-vertical-menu-area {

    .qodef-sc-dropdown {
        display: none !important;
    }
}
/* ==========================================================================
   WooCommerce Dropdown Cart styles - end
   ========================================================================== */