/* ==========================================================================
   WooCommerce Widgets styles - begin
   ========================================================================== */

.widget.woocommerce {
    clear: both;
    
    &.widget_rating_filter,
    &.widget_shopping_cart,
    &.widget_layered_nav,
    &.widget_layered_nav_filters,
    &.widget_product_categories,
    &.widget_products,
    &.widget_recently_viewed_products,
    &.widget_recent_reviews,
    &.widget_top_rated_products  {
        
        ul {
            padding: 0;
            margin: 0;
            list-style: none;
            
            li {
                @include qodefRelativeHolderLayout();
                padding: 0;
                margin: 6px 0 7px;
                clear: both;
                
                &:last-child {
                    margin: 6px 0 0;
                }
                
                ul {
                    margin: 14px 0 0 10px;
                }
            }
        }
    }
    
    &.widget_shopping_cart {
        
        .widget_shopping_cart_content {
            @include qodefRelativeHolderLayout();
            
            ul {
                
                li {
                    
                    img {
                        display: block;
                        max-width: 84px;
                        margin: 0 70% 13px 0;
                    }
                    
                    .remove {
                        position: absolute;
                        top: 0;
                        right: 0;
                        font-size: 0;
                        line-height: 1;
                        
                        &:after {
                            position: relative;
                            display: block;
                            padding: 0 3px;
                            font-family: 'Ionicons';
                            content: "\f404";
                            font-size: 30px;
                            line-height: 25px;
                        }
                    }
                }
            }
            
            .variation {
                @include qodefRelativeHolderLayout();
                
                > * {
                    display: inline-block;
                    vertical-align: middle;
                    margin: 0;
                    
                    p {
                        margin: 0;
                    }
                }
            }
            
            .total {
                margin: 17px 0 13px;
            }
            
            .buttons {
                margin: 10px 0 0;
            }
        }
    }
    
    &.widget_layered_nav {
    
        .qodef-widget-title {
            margin: 0 0 17px;
        }
        
        ul {
            
            li {
                margin: 2px 0;
                
                &.chosen {
                    
                    a {
                        color: $first-main-color;
                    }
                }
            }
        }
    }
    
    &.widget_layered_nav_filters {
        
        .qodef-widget-title {
            margin: 0 0 17px;
        }
    }
    
    &.widget_price_filter {
        
        form {
            margin-top: 22px;
        }
        
        .price_slider_wrapper {
            
            .ui-widget-content {
                position: relative;
                height: 1px;
                background-color: #bfbfbf;
                
                .ui-slider-handle {
                    position: absolute;
                    top: -3px;
                    height: 7px;
                    width: 7px;
                    background-color: $default-heading-color;
                    margin: 0 0 0 -1px;
                    cursor: pointer;
                    outline: medium none;
                    z-index: 2;
                    
                    &:last-child {
                        transform: translateX(-50%);
                    }
                }
                
                .ui-slider-range {
                    position: absolute;
                    display: block;
                    height: 2px;
                    background-color: $default-heading-color;
                    z-index: 1;
                }
            }
        }
        
        .price_slider_amount {
            margin: 14px 0 0;
            
            .button {
                @include qodefButtonSimpleStyle();
                float: right;
                
                &:before,
                &:after {
                    left: 0 !important;
                    right: 0 !important;
                    bottom: 0 !important;
                }
                
                &:hover {
                    color: $default-heading-color;
                    background-color: transparent;
                }
            }
            
            .price_label {
                float: left;
                font-size: 14px;
                font-style: normal;
                line-height: 27px;
            }
        }
    }
    
    &.widget_product_categories {
        
        ul {
            
            li {
                margin: 6px 0 7px;
                
                &:hover {
                    a, span {
                        color: $first-main-color;
                        transition: color 0.3s;
                    }
                    
                    a {
                        &:after {
                            opacity: 1;
                        }
                    }
                }
                
                a {
                    font-size: 17px;
                    position: relative;
                    
                    &:after {
                        content: '';
                        display: block;
                        position: absolute;
                        left: 0;
                        bottom: 0;
                        width: 100%;
                        height: 1px;
                        background-color: currentColor;
                        opacity: 0;
                        transition: opacity .3s;
                    }
                    

                }
                
                ul {
                    margin: 0 0 0 10px;
                }
            }
        }
    }
    
    &.widget_products,
    &.widget_recently_viewed_products,
    &.widget_recent_reviews,
    &.widget_top_rated_products {
    
        .qodef-widget-title {
            margin: 0 0 29px;
        }
        
        ul {
            
            li {
                min-height: 110px;
                
                > span {
                    &:nth-child(3) {
                        margin: 0;
                    }
                    
                    &.amount {
                        &:nth-child(3) {
                            margin-left: 102px;
                        }
    
                        &:nth-child(4) {
                            margin-left: 0;
                        }
                    }
                }
                
                a {
                    
                    img {
                        position: absolute;
                        top: 0;
                        left: 0;
                        width: 100%;
                        max-width: 85px;
                    }
                    
                    span {
                        position: relative;
                        padding: 20px 0 1px 102px;
                        line-height: 20px;
                        box-sizing: border-box;
                    }
                }
                
                > *:not(a) {
                    margin: 0 0 0 102px;
                    line-height: 20px;
                    display: inline-block;
                    float: none;
                }
                
                .star-rating {
                    display: none;
                }
                
                del,
                ins {
                    display: inline-block;
                    
                    span {
                        color: inherit;
                    }
                }
                
                ins {
                    text-decoration: none;
                    margin: 0;
                }
            }
        }
    }
    
    &.widget_products,
    &.widget_recently_viewed_products,
    &.widget_top_rated_products {
        
        ul {
            
            li {
    
                a {
                    &:hover {
                        color: currentColor;
                    }
                }
                
                .product-title {
                    color: $default-heading-color;
                    
                    &:hover {
                        color: $first-main-color;
                        transition: color 0.3s ease-in-out;
                    }
                }
                
                .amount {
                    color: $default-heading-color;
                }
            }
        }
    }
    
    &.widget_recently_viewed_products {
        
        ul {
            
            li {
                
                del,
                > .amount {
                    display: block;
                    margin-top: 10px;
                }
            }
        }
    }
    
    &.widget_recent_reviews {
        
        ul {
            
            li {
                
                .star-rating {
                    margin-top: 7px;
                    margin-bottom: 10px;
                }
            }
        }
        
        a {
            display: block;
            padding: 0 0 0 102px;
        }
        
        .reviewer {
            display: inline-block;
            vertical-align: middle;
            width: auto;
            clear: both;
        }
    }
    
    &.widget_top_rated_products {
        
        ul {
            
            li {
                
                .star-rating {
                    margin-top: 10px;
                    margin-bottom: 12px;
                }
                
                .amount {
                    display: block;
                }
            }
        }
    }
    
    &.widget_product_tag_cloud {
        
        .tagcloud {
            
            a {
                position: relative;
                display: inline-block;
                vertical-align: middle;
                font-family: inherit;
                background-color: transparent;
                font-size: 14px !important;
                
                &:after {
                    content: '/';
                    color: $default-text-color;
                    margin: 0 0 0 4px;
                }
                
                &:last-child {
                    
                    &:after {
                        display: none;
                    }
                }
            }
        }
    }
    
    &.widget_product_search {
        
        .woocommerce-product-search {
            @include qodefRelativeHolderLayout();
            clear: both;
            
            > div {
                @include qodefRelativeHolderLayout();
                border: 1px solid $default-border-color;
                box-sizing: border-box;
            }
            
            input,
            button {
                position: relative;
                display: inline-block;
                vertical-align: top;
                float: left;
                margin: 0;
                height: 54px;
                line-height: 32px;
                color: $default-text-color;
                background-color: transparent;
                border: 0;
                border-radius: 0;
                outline: 0;
                -webkit-appearance: none;
                box-sizing: border-box;
                transition: color .2s ease-out;
            }
            
            input {
                width: calc(100% - 40px);
                padding: 10px 10px 10px 20px;
                font-family: inherit;
                font-size: 13px;
    
                @include qodefPlaceholder {
                    color: inherit;
                }
            }
            
            button {
                width: 40px;
                padding: 10px 13px 10px 4px;
                font-size: 14px;
                cursor: pointer;
                
                &:hover {
                    color: $default-heading-color;
                }
                
                span {
                    display: block;
                    line-height: inherit;
                    
                    &:before {
                        display: block;
                        line-height: inherit;
                    }
                }
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Widgets styles - end
   ========================================================================== */