/* ==========================================================================
   WooCommerce Pagination styles - begin
   ========================================================================== */

.woocommerce-pagination {
	@include qodefStandardPaginationStyle('shop');
	margin: 0;
	
	@include ipad-landscape {
		margin: 0 0 65px;
	}
	
	ul {
		text-align: left;
		
		li span {
			position: relative;
			padding: 0 5px;
			font-weight: bold;
			
			&:after {
				content: '';
				width: 100%;
				height: 1px;
				background-color: currentColor;
				position: absolute;
				bottom: 0;
				left: 0;
			}
		}
	}
}

/* ==========================================================================
   WooCommerce Pagination styles - end
   ========================================================================== */