/* ==========================================================================
   WooCommerce Product List page styles - begin
   ========================================================================== */

.qodef-pl-main-holder,
.related.products,
.upsells.products,
.cross-sells,
.qodef-content .woocommerce {
    @include qodefRelativeHolderLayout();
}

.woocommerce-page {
    .qodef-content{
        .qodef-pl-main-holder{
            a.button,
            .added_to_cart{
                
                &:before {
                    left: 0;
                    right: 0;
                    bottom: 0;
                }
            }
        }
    }
}

.qodef-pl-main-holder {
    
    .qodef-woo-pl-info-on-image-hover & {
        margin: 0 0 50px;
    }
}

ul.products {
    list-style: none;
	margin: 0 -15px;
    clear: both;
    
    > .product {
        position: relative;
        display: inline-block;
	    vertical-align: middle;
	    float: left;
	    margin: 0 0 30px;
	    padding: 0 15px;
	    box-sizing: border-box;
        
        .touch & {
            cursor: pointer;
        }
    
        .qodef-pl-text-wrapper {
            overflow: hidden;
        
            .add_to_cart_button,
            .button,
            .added_to_cart{
                opacity: 0;
                transform: translateX(-100%);
                transition: all .4s cubic-bezier(0,.95,.77,1);
                
                &:before,
                &:after {
                    left: 0;
                    right: 0;
                    bottom: 0;
                }
            }
        }
    
        .price{
            transition: all .3s ease;
        }
    
        &:hover {
        
            .qodef-pl-inner .qodef-pl-text {
                transform: translateX(0);
                opacity: 1;
            }
        
            .qodef-pl-text-wrapper {
                .add_to_cart_button,
                .button,
                .added_to_cart {
                    opacity: 1;
                    transform: translateX(0);
                }
            }
        
            .price{
                opacity: 0;
                transform: translateX(100%);
            }
        }
        
        .qodef-product-list-title {
            margin: 0;
            display: inline-block;
            float: left;
            width: 50%;
            font-size: 20px;
        }
        
        .qodef-pl-rating-holder {
            @include qodefRelativeHolderLayout();
            line-height: 1;
            margin: 10px 0 3px;
        }
        
        .price {
            @include qodefWooCommercePriceStyle();
            position: absolute;
            top: 3px;
            right: 0;
            display: inline-block;
            text-align: right;
            width: 50%;
            margin: 0;
            vertical-align: middle;
            font-size: 20px;
            font-weight: bold;
        }
        
        .qodef-pl-category {
            display: block;
            width: 100%;
            float: left;
            font-size: 17px;
            line-height: 1em;
        }
        
        .button,
        .added_to_cart {
            margin: 25px 0 0;
    
            .qodef-woo-pl-info-below-image & {
                margin: 0;
            }
        }
        
        .button.added {
            display: none;
        }
        
        .qodef-pl-inner {
            @include qodefRelativeHolderLayout();
            overflow: hidden;
            
            .qodef-pl-image {
                @include qodefRelativeHolderLayout();
                overflow: hidden;
                
                img {
                    display: block;
                    width: 100%;
                }
            }
            
            .qodef-pl-text {
                @include qodefAbsoluteHolderLayout();
                padding: 10px;
                opacity: 0;
                background-color: rgba($default-heading-color, 0.6);
                box-sizing: border-box;
                transition: opacity .2s ease-out;
            }
            
            .qodef-pl-text-outer {
                @include qodefTableLayout();
                text-align: center;
            }
            
            .qodef-pl-text-inner {
                @include qodefTableCellLayout();
                
                .qodef-product-list-title,
                .price,
                .star-rating {
                    color: #fff;
                }
                
                .price del {
                    color: rgba(#fff, .8);
                }
            }
            
            > a {
                @include qodefAbsoluteHolderLayout();
            }
        }
        
        .qodef-pl-text-wrapper {
            @include qodefRelativeHolderLayout();
            margin: 22px 0 38px;
    
            .add_to_cart_button,
            .button,
            .added_to_cart {
                position: absolute;
                top: 0;
                right: 0;
                padding: 0;
                opacity: 0;
                color: $default-heading-color;
                background-color: transparent;
                transition: all .5s;
                
                &:after {
                    content: '';
                    display: block;
                    position: absolute;
                    left: 0;
                    bottom: 0;
                    width: 100%;
                    height: 1px;
                    background-color: currentColor;
                }
            }
            
        }

        > a {
            @include qodefAbsoluteHolderLayout();
        }
    }
}

$columns_space_label: ('no', 'tiny', 'small', 'normal', 'medium', 'large');
$columns_space_width: (0, 5, 10, 15, 20, 25);

@for $i from 0 to length($columns_space_label) {
    &.qodef-woo-#{nth($columns_space_label,$i+1)}-space {
        $column_width: nth($columns_space_width,$i+1);
        $column_width_margin: $column_width * 2;
        
        ul.products {
            @if($column_width == 0) {
                margin: 0;
            } @else {
                margin: 0 -#{$column_width}px;
            }
            
            > .product {
                padding: 0 #{$column_width}px;
                margin: 0 0 #{$column_width_margin}px;
            }
        }
    }
}

.qodef-pl-main-holder,
.related.products,
.upsells.products,
.cross-sells {
    
    $product_list_columns: ('columns-3', 'columns-4');
    
    @for $i from 0 to length($product_list_columns) {
        $column_width: 100%/($i+3);
        
        > .products {
            
            > .product {
                
                @media only screen and (min-width: $ipad-landscape-plus-pixel) {
                    
                    .qodef-woocommerce-#{nth($product_list_columns,$i+1)} & {
                        width: $column_width;
                        
                        &:nth-child(#{$i+3}n+1) {
                            clear: both;
                        }
                    }
                }
            }
        }
    }
}

.woocommerce {
    
    &.columns-1 {
        
        > .products {
            
            > .product {
                float: none;
                width: 100%;
            }
        }
    }
    
    $product_list_vc_columns: ('columns-2', 'columns-3', 'columns-4', 'columns-5', 'columns-6');
    
    @for $i from 0 to length($product_list_vc_columns) {
        &.#{nth($product_list_vc_columns,$i+1)} {
            $column_vc_width: 100%/($i+2);
            
            > .products {
                
                > .product {
                    width: $column_vc_width;
                    $product_list_vc_columns_label: #{nth($product_list_vc_columns,$i+1)};
                    
                    @if $product_list_vc_columns_label == 'columns-5' {
                        @media only screen and (min-width: $laptop-landscape-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    } @else if $product_list_vc_columns_label == 'columns-6' {
                        @media only screen and (min-width: $laptop-landscape-large-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    } @else if $product_list_vc_columns_label == 'columns-3' {
                        @media only screen and (min-width: $ipad-portrait-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    } @else if $product_list_vc_columns_label == 'columns-2' {
                        @media only screen and (min-width: $phone-landscape-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    } @else {
                        @media only screen and (min-width: $ipad-landscape-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    }
                }
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Product List page styles - end
   ========================================================================== */