/* ==========================================================================
   Product List shortcode styles - begin
   ========================================================================== */

.qodef-pl-holder {
    @include qodefRelativeHolderLayout();
	opacity:0;
	
	&.qodef-info-on-image {
		
		.qodef-pli {
			
			.qodef-pli-add-to-cart {
				margin: 25px 0 0;
			}
		}
	}
	
    .qodef-pli {
	    position: relative;
	    display: inline-block;
	    vertical-align: top;
	    float: left;
	    width: 100%;
        padding: 0;
        z-index: 100;
	    box-sizing: border-box;
	    @include qodefTransform(translate3d(0,200px,0));
	    will-change: transform;
	    
	    &.qodef-pli-animated {
		    @include qodefTransform(translate3d(0,0,0));
		    -webkit-transition: -webkit-transform 2s cubic-bezier(0,0,0,1), opacity .6s;
		    transition: transform 2s cubic-bezier(0,0,0,1), opacity .6s;
	    }
	    
	    .qodef-pli-title {
		    margin: 0 0 4px;
	    }
	
	    .qodef-pli-category {
		    margin: 11px 0 3px;
	    }

        .qodef-pli-excerpt {
            color: $default-heading-color;
	        line-height: 1.3em;
            margin: 11px 0 3px;
        }

	    .qodef-pli-rating-holder {
		    @include qodefRelativeHolderLayout();
		    line-height: 1;
		    margin: 10px 0 3px;
	    }

	    .qodef-pli-rating {
		    @include qodefWooCommerceRatingsStyle();
	    }

        .qodef-pli-price {
	        @include qodefWooCommercePriceStyle();
        }
	    
	    &.qodef-pli-has-switch-image {
		    .qodef-pli-switch-image {
			    position: absolute;
			    top: 0;
			    left: 0;
			    opacity: 0;
		    }
		
		    .qodef-pli-image {
			    img {
			    }
		    }
		    
		    .qodef-pli-inner {
			    &:hover {
				    .qodef-pli-image img {
					    opacity: 0;
				    }
				
				    .qodef-pli-switch-image {
					    opacity: 1;
				    }
			    }
		    }
	    }
    }

    .qodef-pli-inner {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        overflow: hidden;

        &:hover {

            .qodef-pli-text {
                opacity: 1;
            }
        }

        .touch & {
            cursor: pointer;
        }

        .qodef-pli-image {
            @include qodefRelativeHolderLayout();

            img {
                display: block;
                width: 100%;
            }

            .qodef-pli-onsale,
            .qodef-pli-out-of-stock,
            .qodef-pli-new-product {
	            @include qodefWooCommerceMarksGeneralStyle();
            }

            .qodef-pli-onsale {
	            @include qodefWooCommerceSaleStyle();
            }

            .qodef-pli-out-of-stock {
	            @include qodefWooCommerceOutOfStockStyle();
            }
	
	        .qodef-pli-new-product {
		        @include qodefWooCommerceNewProductStyle();
	        }
        }
            
        .qodef-pli-text {
            @include qodefAbsoluteHolderLayout();
            padding: 10px;
            opacity: 0;
            background-color: rgba($default-heading-color, 0.6);
            box-sizing: border-box;
            transition: opacity .2s ease-out;
        }

        .qodef-pli-text-outer {
	        @include qodefTableLayout();
	        text-align: center;
        }

        .qodef-pli-text-inner {
            @include qodefTableCellLayout();

            .qodef-pli-title,
            .qodef-pli-category a,
            .qodef-pli-excerpt,
            .qodef-pli-rating,
            .qodef-pli-price {
                color: #fff;
            }
	
	        .qodef-pli-category a:hover {
		        color: rgba(#fff, .8);
	        }

	        .qodef-pli-add-to-cart {

		        &.qodef-default-skin {

			        .button,
			        .added_to_cart {
				        @include qodefButtonSolidColor();
			        }
		        }

		        &.qodef-light-skin {

			        .button,
			        .added_to_cart {
				        color: $default-heading-color;
				        background-color: #fff;

				        &:hover {
					        color: #fff;
					        background-color: $first-main-color;
				        }
			        }
		        }
		
		        &.qodef-dark-skin {
			
			        .button,
			        .added_to_cart {
				        color: #fff;
				        background-color: $default-heading-color;
				
				        &:hover {
					        color: #fff;
					        background-color: $first-main-color;
				        }
			        }
		        }
	        }

            .button,
            .added_to_cart {
	            @include qodefButtonDefaultStyle();
	            @include qodefButtonSize();
	            white-space: nowrap;
	            z-index: 3;
            }

            .button.added {
                display: none;
            }
        }

	    .qodef-pli-link {
		    @include qodefAbsoluteHolderLayout();
	    }
    }

    .qodef-pli-text-wrapper {
        @include qodefRelativeHolderLayout();
        margin: 22px 0 0;
    }
	
	&.qodef-info-below-image {
		.qodef-pli-text-wrapper {
			text-align: center;
		}
		
		.qodef-pli-add-to-cart {
			
			%line {
				content: '';
				position: absolute;
				left: 20px;
				right: 20px;
				bottom: 8px;
				height: 1px;
				background-color: currentColor;
			}
			
			a {
				@include qodefButtonOutlineColor();
				position: relative;
				display: inline-block;
				margin-top: 19px;
				padding: 6px 20px 7px;
				font-size: 17px;
				transition: all 0.2s;
				
				&:before {
					@extend %line;
					@include qodefTransform(scaleX(0));
					transform-origin: 0 0;
				}
				
				&:after {
					@extend %line;
					transform-origin: 100% 0;
				}
				
				&:hover {
					&:before {
						@include qodefTransform(scaleX(1));
						transition: transform .9s cubic-bezier(0.19, 1, 0.22, 1) .25s;
					}
					
					&:after {
						@include qodefTransform(scaleX(0));
						transition: transform .9s cubic-bezier(0.19, 1, 0.22, 1);
					}
				}
				
				&.added {
					display: none;
				}
			}
		}
		
		.qodef-pli {
			
			.qodef-pli-title {
				margin: 1px 0 4px;
				font-size: 26px;
				line-height: 1.03em;
			}
			
			.qodef-pli-price * {
				font-size: 17px;
				line-height: 1.58em;
				font-weight: 400;
				color: $default-heading-color;
			}
		}
	}
	
	&.qodef-product-info-light {
		
		.qodef-pli-inner {
			
			.qodef-pli-text-inner {
				
				.qodef-pli-title,
				.qodef-pli-category a,
				.qodef-pli-excerpt,
				.qodef-pli-rating,
				.qodef-pli-price {
					color: #fff;
				}
				
				.qodef-pli-category a:hover {
					color: rgba(#fff, .8);
				}
			}
		}
	}
	
	&.qodef-product-info-dark {
		
		.qodef-pli-inner {
			
			.qodef-pli-text-inner {
				
				.qodef-pli-title,
				.qodef-pli-category a,
				.qodef-pli-excerpt,
				.qodef-pli-rating,
				.qodef-pli-price {
					color: $default-heading-color;
				}
				
				.qodef-pli-category a:hover {
					color: rgba($default-heading-color, .8);
				}
			}
		}
	}
	
	/*************** Masonry Layout - begin ***************/
	
	&.qodef-masonry-layout {
		
		.qodef-pli-inner {
			width: 100%;
			height: 100%;
			
			.qodef-pli-image {
				height: 100%;
			}
		}
	}
	
	/*************** Masonry Layout - end ***************/
	
	&.qodef-pl-animation-disabled {
		opacity: 1;
		
		.qodef-pli {
			@include qodefTransform(translate3d(0, 0, 0));
		}
	}
}
/* ==========================================================================
   Product List shortcode styles - end
   ========================================================================== */