/* ==========================================================================
   WooCommerce Buttons styles - begin
   ========================================================================== */

.woocommerce-page .qodef-content,
div.woocommerce {

    %line {
        content: '';
        position: absolute;
        left: 32px;
        right: 32px;
        bottom: 12px;
        height: 1px;
        background-color: currentColor;
    
        @include ipad-landscape {
            left: 20px;
            right: 20px;
            bottom: 8px;
        }
    }

    a.button,
    a.added_to_cart,
    input[type="submit"],
    button[type="submit"]:not(.qodef-search-submit),
    .wc-forward:not(.added_to_cart):not(.checkout-button) {
        @include qodefButtonDefaultStyle();
        @include qodefButtonSize();
        @include qodefButtonSolidColor();
        cursor: pointer;
        z-index: 3;
        line-height: 1.2em;
    
        &:before {
            @extend %line;
            @include qodefTransform(scaleX(0));
            transform-origin: 0 0;
        }
    
        &:after {
            @extend %line;
            transform-origin: 100% 0;
        }
        
        &:hover {
            &:before {
                @include qodefTransform(scaleX(1));
                transition: transform .9s cubic-bezier(0.19, 1, 0.22, 1) .25s;
            }
    
            &:after {
                @include qodefTransform(scaleX(0));
                transition: transform .9s cubic-bezier(0.19, 1, 0.22, 1);
            }
        }
        
    }
    
    a.button.added {
        display: none;
    }
}

div.woocommerce {
    a.button {
        &:before,
        &:after {
            left: 0;
            right: 0;
            bottom: 0;
        }
    }

    ul.products{
        li.product{
            a.button {
                &:before,
                &:after {
                    left: 0;
                    right: 0;
                    bottom: 0;
                }
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Buttons styles - end
   ========================================================================== */